html {
	font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Helvetica Neue, Arial, Noto Sans, sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol, Noto Color Emoji;
	line-height: 1.5
}
body {
	margin: 0
}
* {
	box-sizing: border-box;
	border: 0 solid #e2e8f0
}
img {
	border-style: solid
}
img,
video {
	display: block;
	vertical-align: middle;
	max-width: 100%;
	height: auto
}
h1,
h2,
h3 {
	font-size: inherit;
	font-weight: inherit
}
@font-face {
	font-family: Futura-medium;
	src: url('https://cdn.shopify.com/s/files/1/0316/1119/1435/files/futura_medium_bt.ttf?23723') format("woff2");
	font-weight: 400
}
.head-para {
	font-family: futura-medium;
}
.cta-primary {
	padding-top: .75rem;
	padding-bottom: .75rem;
	border-width: 3px;
	border-color: #000;
	color: #000;
	font-size: .875rem;
	letter-spacing: .1em
}
.cta-primary-white {
	border-color: #fff;
	color: #fff
}
.cta-primary-black {
	border-color: #000;
	color: #fff !important;
}
.cta-primary-black:active {
	color: #fff !important;
}
.cta-primary-white.active {
	color: #919191 !important;
}
.cta-primary-white:hover {
	color: #fff;
}
/** Cover images with Gradient**/

.cover-imgs {
	-webkit-animation: fadein 1.5s;
	padding-top: 36.458%;
}
.material-cover {
	background-image: linear-gradient(rgba(0, 0, 0, -0.7), rgba(0, 0, 0, 0.3)), url("https://cdn.shopify.com/s/files/1/0114/4120/9408/files/materials-hero-d.jpg?v=1582317460");
}
.strength-cover {
	background-image: linear-gradient(rgba(0, 0, 0, -0.7), rgba(0, 0, 0, 0.3)), url("https://cdn.shopify.com/s/files/1/0114/4120/9408/files/strength-hero-d.jpg?v=1582227701");
}
.people-cover {
	background-image: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url('https://cdn.shopify.com/s/files/1/0114/4120/9408/files/people-hero-d.jpg?v=1582667120');
}
.story-cover {
	background-image: linear-gradient(rgba(0, 0, 0, -0.7), rgba(0, 0, 0, 0.3)), url("https://cdn.shopify.com/s/files/1/0114/4120/9408/files/story-hero-d-v2.jpg?v=1583962376");
}
.strength-banner {
	background-image: url("https://cdn.shopify.com/s/files/1/0114/4120/9408/files/strength-aisha-d.jpg?v=1582227701");
}
.story-bottom__txt {
	left: 35%;
	color: #fff;
}
.story-bottom {
	background: rgba(0, 0, 0, 0.8);
}
.story-bottom img {
	opacity: 0.75;
}
@media (max-width: 640px) {
	.container {
		width: 100%;
		max-width: 100%;
	}
	.cta-primary {
		font-size: 0.75rem;
	}
	.cover-imgs {
		padding-top: 112.125%;
	}
	.people-cover {
		background-image: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url('https://cdn.shopify.com/s/files/1/0114/4120/9408/files/people-hero-m_2a6cc106-fb10-4c08-872d-4cb34aaded94.jpg?v=1582906411');
	}
	.material-cover {
		background-image: linear-gradient(rgba(0, 0, 0, -0.7), rgba(0, 0, 0, 0.3)), url("https://cdn.shopify.com/s/files/1/0114/4120/9408/files/materials-hero-m.jpg?v=1582317460");
	}
	.strength-cover {
		background-image: linear-gradient(rgba(0, 0, 0, -0.7), rgba(0, 0, 0, 0.3)), url("https://cdn.shopify.com/s/files/1/0114/4120/9408/files/strength-hero-m.jpg?v=1582227701");
	}
	.story-cover {
		background-image: url("https://cdn.shopify.com/s/files/1/0114/4120/9408/files/story-hero-m-v2.jpg?v=1583962376");
	}
	.strength-banner {
		background-image: url("https://cdn.shopify.com/s/files/1/0114/4120/9408/files/strength-aisha-m.jpg?v=1582227701");
		padding-top: 143.125%;
	}
	.story-bottom__txt {
		top: 35%;
		left: 5%;
		color: #fff;
	}
}
